import { useHttp } from "../hooks/http.hook";

const useMarvelService = () => {
	const _apiBabe = 'https://gateway.marvel.com:443/v1/public/';
	const _apiKey = 'apikey=744a96d18fccecddd408e2d153c70566';
	const _baseOffset = 210;

	const {loading, error, request, clearError} = useHttp();

	const getAllCharacters = async (offset = _baseOffset) => {
		const res = await request(`${_apiBabe}characters?limit=9&offset=${offset}&${_apiKey}`);
		return res.data.results.map(_transformCharacter)
	}

	const getCharacter = async (id) => {
		const res = await request(`${_apiBabe}characters/${id}?${_apiKey}`);
		return _transformCharacter(res.data.results[0]);
	}

	const getAllComics = async (offset = _baseOffset) => {
		const res = await request(`${_apiBabe}comics?limit=8&offset=${offset}&${_apiKey}`);
		return res.data.results.map(_transformComics);
	}

	const getComic = async (id) => {
		const res = await request(`${_apiBabe}comics/${id}?${_apiKey}`);
		return _transformComics(res.data.results[0]);
	}

	const _transformComics = (comics) => {
		return {
			thumbnail: `${comics.thumbnail.path}.${comics.thumbnail.extension}`,
			title: comics.title,
			price: `${comics.prices[0].price ? comics.prices[0].price : 'NOT AVAILABLE'}`,
			id: comics.id,
			description: comics.description ? comics.description : 'There is no description for this comics',
			pageCount: comics.pageCount ? `${comics.pageCount} pages` : "No information about the number of pages",
			language: comics.textObjects[0]?.language || "en-us"
		}
	}

	const _transformCharacter = (char) => {
		return {
			name: char.name,
			description: char.description ? `${char.description.slice(0, 210)}...` : 'There is no description for this character',
			thumbnail: `${char.thumbnail.path}.${char.thumbnail.extension}`,
			homepage: char.urls[0].url,
			wiki: char.urls[1].url,
			id: char.id,
			comics: char.comics.items,
		}
	}

	return {loading, error, clearError, getCharacter, getAllCharacters, getAllComics, getComic};
}

export default useMarvelService;