import { useState, useEffect, useRef } from 'react';
import PropTypes from 'prop-types';

import useMarvelService from '../../services/MarvelService';
import ErrorMessage from '../error/ErrorMessage';
import Spinner from '../spinner/Spinner';

import './charList.scss';

// char__item_selected

const CharList = (props) => {
	const [listChars, setListChars] = useState([]);
	const [newCharsLoading, setNewCharsLoading] = useState(false);
	const [offset, setOffset] = useState(210);
	const [ended, setEnded] = useState(false);

	const {loading, error, clearError, getAllCharacters} = useMarvelService();


	// const initScroll = (e) => {
	// 	const root = document.getElementById('root');
	// 	const { scrollY, innerHeight } = window;
	// 	const currentScroll = scrollY + innerHeight;
	// 	const pageHeight = root.scrollHeight;

	// 	if (currentScroll >= pageHeight) {
	// 		this.onLoadMore(this.state.offset + 9);
	// 	}
	// }

	useEffect(() => {
		onLoadMore();
		console.log('use');
	}, [])

	const onLoadMore = (offset) => {
		console.log('more');
		setNewCharsLoading(true);
		clearError();

		getAllCharacters(offset)
			.then(onCharsLoaded)
	}

	const onCharsLoaded = (newListChars) => {
		let ended = false;
		if (newListChars.length < 9) {
			ended = true;
		}

		setListChars((listChars) => [...listChars, ...newListChars]);
		setNewCharsLoading(newItemLoading => false);
		setOffset(offset => offset + 9);
		setEnded(charEnded => ended);
	}

	const itemRefs = useRef([]);

	const focusOnItem = (id) => {
		// Я реализовал вариант чуть сложнее, и с классом и с фокусом
		// Но в теории можно оставить только фокус, и его в стилях использовать вместо класса
		// На самом деле, решение с css-классом можно сделать, вынеся персонажа
		// в отдельный компонент. Но кода будет больше, появится новое состояние
		// и не факт, что мы выиграем по оптимизации за счет бОльшего кол-ва элементов

		// По возможности, не злоупотребляйте рефами, только в крайних случаях
		itemRefs.current.forEach(item => item.classList.remove('char__item_selected'));
		itemRefs.current[id].classList.add('char__item_selected');
		itemRefs.current[id].focus();
  }

	const renderItems = (arr) => {
		const items = arr.map((item, i) => {
			const { name, id, thumbnail } = item;

			let imgStyle = {'objectFit' : 'cover'};
			if (item.thumbnail === 'http://i.annihil.us/u/prod/marvel/i/mg/b/40/image_not_available.jpg') {
				imgStyle = {'objectFit' : 'contain'};
			}

			return (
				<li
				key={id}
					ref={(el) => itemRefs.current[i] = el}
					tabIndex={0}
					className='char__item'
					onClick={() => {
						props.onSelectedId(id);
						focusOnItem(i);
					}}
					onKeyUp={(e) => {
						if (e.key === ' ' || e.key === "Enter") {
							 props.onSelectedId(id);
							 focusOnItem(i);
						}
				  }}>
					<img src={thumbnail} alt={name} style={imgStyle}/>
					<div className="char__name">{name}</div>
				</li>
			)
		})
		return (
			<ul className="char__grid">
				{items}
			</ul>
		)
	};

	const spinner = loading ? <Spinner/> : null;
	const errorMessage = error ? <ErrorMessage/> : null;
	const componentsList = renderItems(listChars)

	return (
		<div className="char__list">
			{spinner}
			{errorMessage}
			{componentsList}
				<button
				className="button button__main button__long"
				onClick={() => onLoadMore(offset)}
				disabled={newCharsLoading}
				style={{'display': ended ? 'none' : 'block'}}>
					<div className="inner">load more</div>
				</button>
		</div>
	)
}

CharList.propTypes = {
	onSelectedId: PropTypes.func.isRequired
}

export default CharList;